.about-me {
  padding: 110px 70px;
}

.about-me__header {
  margin: 0 0 70px 0;
  padding: 0 0 23px;
  border-bottom: 1px solid #000;
  font-size: 22px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: -0.04em;
}

.about-me__container {
  display: flex;
  justify-content: space-between;
}

.about-me__name {
  margin: 0;
  font-size: 50px;
  font-weight: 400;
  line-height: 58px;
  letter-spacing: -0.04em;
}

.about-me__age {
  margin: 20px 0 26px;
  font-size: 18px;
  font-weight: 500;
  line-height: 20px;
}

.about-me__description {
  max-width: 600px;
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
}

.about-me__links {
  display: flex;
  margin: 99px 0 0;
  padding: 0;
  list-style: none;
}

.about-me__link {
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  margin-right: 20px;
  color: #000;
  transition: opacity .2s linear;
}

.about-me__link:hover {
  opacity: 60%;
}

.about-me__avatar {
  max-width: 270px;
  max-height: 327px;
  margin-left: 50px;
  width: 100%;
  object-fit: fill;
  border-radius: 10px;
}

@media screen and (max-width: 900px) {
  .about-me {
    padding: 90px 50px;
  }

  .about-me__name {
    font-size: 40px;
    line-height: 40px;
  }

  .about-me__age {
    margin: 16px 0 20px;
    font-size: 12px;
    line-height: 18px;
  }

  .about-me__description {
    max-width: 364px;
    font-size: 12px;
    line-height: 18px;
  }

  .about-me__links {
    margin: 87px 0 0;
  }

  .about-me__avatar {
    max-width: 255px;
    max-height: 307px;
  }
}

@media screen and (max-width: 650px) {
  .about-me {
    padding: 70px 14px;
  }

  .about-me__header {
    padding-bottom: 28px;
    font-size: 18px;
    line-height: 22px;
  }

  .about-me__container {
    margin-top: 60px;
    flex-direction: column-reverse;
    align-items: center;
  }

  .about-me__name {
    font-size: 30px;
    line-height: 36px;
  }

  .about-me__age {
    margin: 20px 0;
    font-size: 11px;
    line-height: 16px;
  }

  .about-me__description {
    font-size: 11px;
    line-height: 16px;
  }

  .about-me__links {
    margin: 40px 0 0 0;
    font-size: 14px;
    line-height: 17px;
  }

  .about-me__link {
    margin-right: 26px;
  }

  .about-me__avatar {
    margin: 0 0 40px 0;
    max-width: 292px;
    max-height: 352px;
    height: 100vh;
  }
}
