.portfolio {
  padding: 0 70px 125px 70px;
}

.portfolio__header {
  margin: 0 0 20px;
  color: #a0a0a0;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
}

.portfolio__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.portfolio__project {
  padding: 20px 0 19px;
  border-bottom: 1px solid #e8e8e8;
}

.portfolio__project:last-child {
  border-bottom: none;
}

.portfolio__link {
  display: flex;
  justify-content: space-between;
  background: transparent url(../../../images/arrow.svg) right center / 20px no-repeat;
  color: #000;
  font-size: 30px;
  font-weight: 400;
  line-height: 50px;
  text-decoration: none;
  letter-spacing: -0.04em;
  transition: opacity 0.2s linear;
}

.portfolio__link:hover {
  opacity: 60%;
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  .portfolio {
    padding: 0 50px 90px;
  }

  .portfolio__header {
    font-size: 18px;
    line-height: 20px;
  }

  .portfolio__link {
    background-size: 18px;
    font-size: 28px;
  }
}

@media screen and (max-width: 640px) {
  .portfolio {
    padding: 0 14px 70px;
  }

  .portfolio__header {
    font-size: 14px;
  }

  .portfolio__list {
    margin-top: 21px;
  }

  .portfolio__link {
    background-size: 12px;
    font-size: 18px;
    line-height: 28px;
  }
}
